@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Cinzel:wght@400..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bad+Script&family=Bebas+Neue&family=Cinzel:wght@400..900&family=Darker+Grotesque:wght@300..900&family=Six+Caps&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bad+Script&family=Bebas+Neue&family=Cinzel:wght@400..900&family=Darker+Grotesque:wght@300..900&family=Medula+One&family=Six+Caps&display=swap');


.gallery-page-container-gallery{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0, .2);
    object-fit:contain;
    background: linear-gradient(90deg, #141414 0%, rgb(78, 78, 78) 100%);
    height: 1500px;
    
}

.about-section-container-gallery{
    /* background-color: blueviolet; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    width:40%;
    font-size: larger;
    color: aliceblue;
    margin-top: 0px;
    
}

.form-section-container-gallery{
    background-color: #141414; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    height: 70%;
    width: 500px;
    border: #f5a127 solid 2.5px;
    border-radius: 3%;
    position: sticky;
    /* padding-left: 300px;
    padding-right: 300px; */
}

.form-group{
    background-color: #141414;
    color: #f5a127;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    height:83px;
    width: 450px;
    justify-content: space-between;
    align-items: center;
    /* border: 1.5px solid black; */
    border-radius: 3%;
    padding-top: 3px;
    padding-bottom: 15px;
    font-family: "Darker Grotesque", sans-serif;
    /* font-size: x-large; */

}

.form-group-description{
    background-color: #141414;
    color: #f5a127;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    height:150px;
    width: 450px;
    justify-content: space-between;
    align-items: center;
    /* border: 1.5px solid black; */
    border-radius: 3%;
    padding-top: 3px;
    /* padding-bottom: 15px; */

}

.form-group-btn{
    display: flex;
    justify-content: center;
    margin-top: 50px;
}


h2{
    color: #e8e8e8;
    font-family: "Bebas Neue", sans-serif;
    font-size: 22pt;
    position: relative;
    bottom: 80px;
    margin-top: 0px;
    font-size: 70pt;
}

.h2-form{
    color: white;
    padding-top: 75px;
    font-size: 28pt;
    font-family: "Bebas Neue", sans-serif;
}

span{
    color: #f5a127;
    font-family: "Bebas Neue", sans-serif;
    font-size: 24pt;
    
}

.span-details{
    font-size: x-large;
    font-weight: bold;
    color: #6c8484;
    font-family: "Darker Grotesque", sans-serif;
    font-size: x-large;
    
}

.form-btn{
    background-color: #f5a127;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 85%;
    height:50px;
    font-size: large;
}

.input-box{
    width: 75%;
    font-size: large;
}


#form-description{
    height: 100px;
    width: 80%;
    font-size: large;
    
}

p{
    font-family: "Darker Grotesque", sans-serif;
    font-size: x-large;
}


.gallery-imgs{
    position: relative;
    bottom: 260px;
   width: 1600px;
   height: 500px;
    scale: 0.5;
}

.gallery-title{
    position: relative;
    bottom: 200px;
    /* color: #f5a127; */
}

@media screen and (max-width: 960px) {
    .gallery-page-container-gallery{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        box-shadow: inset 0 0 0 1000px rgba(0,0,0, .2);
        object-fit:contain;
        background: linear-gradient(90deg, #141414 0%, rgb(78, 78, 78) 100%);
        height: 1500px;
        
    }
    
    .about-section-container-gallery{
        /* background-color: blueviolet; */
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        width:40%;
        font-size: larger;
        color: aliceblue;
        margin-top: 0px;
        position: relative;
        left:235px;
        top:150px;
        
    }
    
    .form-section-container-gallery{
        background-color: #141414; 
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        height: 70%;
        width: 500px;
        border: #f5a127 solid 2.5px;
        border-radius: 3%;
        position: sticky;
        visibility: hidden;
        
        /* padding-left: 300px;
        padding-right: 300px; */
    }

    /* .gallery-title{
        visibility: hidden;
    } */

    .gallery-imgs{
        scale:.35;
        width:1000px;
    }

    .p-gallery{
        position: relative;
        bottom: 150px;
    }
}
